@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-size: 1rem;
  font-family: "Poppins", sans-serif;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  overflow: hidden;
  background-color: #fbf9f6;
}

.container {
  text-align: center;
}

.progress__container {
  display: flex;
  justify-content: space-between;
  max-width: 100%;
  width: 350px;
  position: relative;
}
.progress__container::before {
  content: "";
  background-color: #e0e0e0;
  position: absolute;
  top: 25%;
  left: 0;
  transform: translateY(-25%);
  height: 4px;
  width: 100%;
  z-index: -1;
}
.progress__container .progress {
  background-color: #3498db;
  position: absolute;
  top: 25%;
  left: 0;
  transform: translateY(-25%);
  z-index: -1;
  height: 4px;
  width: 0%;
  transition: 0.4s ease;
}
.progress__container .progress__step {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 3px solid #e0e0e0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
  background-color: white;
  transition: 0.4s ease 0.2s;
}
.progress__container .progress__step--active {
  background-color: #3498db;
  color: white;
}

.button__group {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.button__group #prev {
  cursor: not-allowed;
}
.button__group .btn {
  background-color: #3498db;
  color: white;
  padding: 5px 25px;
  outline: none;
  border: 0;
  cursor: pointer;
}
.button__group .btn[disabled] {
  background-color: #e0e0e0;
}
.button__group .btn:active {
  scale: 0.95;
}